import { For } from "solid-js";
import {
  cskData,
  cskDatumToString,
  currentFocus,
  editingStatus,
  setCurrentFocus,
  setEditingStatus,
} from "./setkey";
import {
  commands,
  csk_category,
} from "../../components/custom-shortcut-key/commands";

export function CustomShortcutKeyPage() {
  return (
    <>
      <div>
        <h1></h1>
        <xul:description class="indent tip-caption">
          Floorp Daylight 
          Floorp Daylight 80
          
          Floorp Daylight 
        </xul:description>
        <xul:checkbox label="Firefox " />
      </div>
      <For each={csk_category}>
        {(category) => (
          <>
            <div
              data-l10n-id={"floorp-custom-actions-" + category}
              style={{
                "padding-top": "20px",
              }}
            >
              {category}
            </div>
            <For each={Object.entries(commands)}>
              {([key, value]) =>
                value.type === category ? (
                  <div style={{ display: "flex" }}>
                    <label
                      style={{ "flex-grow": "1" }}
                      data-l10n-id={
                        "floorp-custom-actions-" +
                        key.replace("floorp-", "").replace("gecko-", "")
                      }
                    >
                      {key}
                    </label>
                    <input
                      value={
                        currentFocus() === key && editingStatus() !== null
                          ? editingStatus()!
                          : cskDatumToString(cskData(), key)
                      }
                      onFocus={(ev) => {
                        setCurrentFocus(key);
                      }}
                      onBlur={(ev) => {
                        setEditingStatus(null);
                        if (currentFocus() === key) {
                          setCurrentFocus(null);
                        }
                      }}
                      readonly={true}
                      placeholder="Type a shortcut"
                      style={{
                        "border-radius": "5px",
                        border: "1px solid gray",
                        padding: "6px 10px",
                      }}
                    />
                  </div>
                ) : undefined
              }
            </For>
          </>
        )}
      </For>
    </>
  );
}
