import { insert, render } from "./solid-xul/solid-xul";
import { csk } from "./preference/csk";
import { category } from "./preference/csk/category";
import { initHashChange } from "./preference/hashchange";
import { CustomShortcutKey } from "./components/custom-shortcut-key/index";

console.log("hallo csk");

const init = () => {
  insert(
    document.querySelector("#categories"),
    category(),
    document.getElementById("category-Downloads"),
  );
  render(csk, document.querySelector(".pane-container"));
  initHashChange();
  switch (location.hash) {
    case "#csk": {
      document.getElementById("category-csk")?.click();
      break;
    }
  }
};

const foo = () => {
  if (document.querySelector(".pane-container")) {
    console.log("render 1");
    init();
  } else {
    console.log("render 2");
    init();
  }
};

if (document.readyState !== "loading") {
  foo();
} else {
  document.addEventListener("DOMContentLoaded", foo);
}

